.App {
  text-align: center;

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;

    #author {
      text-align: end;
    }

    #quote-box {
      background-color: white;
      color: black;
      border-radius: 10px;
      padding: 10px;
      max-width: 80%;
      margin: 0 auto; /* Center the box horizontally */
    }
    /* Media query for smaller screens (e.g., mobile devices) */
    @media (max-width: 80px) {
      #quote-box {
        width: 80%; /* Adjust width for smaller screens */
      }
    }
    #tweet-quote {
      display: flex;
      float: left;
    }

    #New-Quote {
      float: right;
      color: white;
      border-radius: 10px;
      cursor: pointer;
      font-style: oblique;
      border: none
    }

    .button {
      flex-direction: column;
    }
  }
}